    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"infoBox"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"unload"</span>&gt;</span>Unload<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"load"</span>&gt;</span>Load<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"reload"</span>&gt;</span>Reload<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
